.content {
  text-transform: uppercase;
  position: absolute;
  top: 32%;
  left: 57%;
  text-shadow: 0.06rem 0 0.06rem #ea36af !important;
  font-size: 0.9rem;
  transform: translate(-50%, -50%);
  font-family: "Asap", sans-serif;
  height: 160px;
  overflow: hidden;

  @include forTablet {
    font-size: 1.5rem;
    top: 25%;
    left: 50%;
  }
  @include forPc {
    font-size: 2rem;
    line-height: 40px;
    color: black;
  }

  &__container {
    width: 300px;
    font-weight: 900;
    overflow: hidden;
    height: 40px;
    padding: 0px 5px;

    p {
      font-size: 1.3rem;
      margin-bottom: 1rem;
      margin-top: 0rem;
      padding: 4px;
      @include forTablet {
        font-size: 1.5rem;
        padding: 2px;
      }
      @include forPc {
        font-size: 2rem;
      }
    }
    @include forTablet {
      padding: 0 40px;
      width: auto;
      
    }

    &:before {
      content: "[";
      left: 0;
      font-size: x-large;
    }

    &:after {
      content: "]";
      right: 60px;
      @include forTablet {
        position: absolute;
        right: 0;
      }
    }

    &:after,
    &:before {
      position: absolute;
      top: 0;
      color: #ff19ad;
      line-height: 40px;
      font-size: x-large;

      @include forTablet {
        font-size: 33px;
      }
      @include forPc {
        font-size: 42px;
      }

      -webkit-animation-name: opacity;
      -webkit-animation-duration: 2s;
      -webkit-animation-iteration-count: infinite;
      animation-name: opacity;
      animation-duration: 2s;
      animation-iteration-count: infinite;
    }

    &__text {
      margin-bottom: 1rem;
      margin-top: 0.6rem;

      float: left;

      @include forTablet {
        margin-top: 0;
        margin: 0;
      }
    }

    &__list {
      margin-top: 0;
      padding-left: 40px;
      font-size: 1.3rem;
      text-align: left;
      list-style: none;

      -webkit-animation-name: change;
      -webkit-animation-duration: 10s;
      -webkit-animation-iteration-count: infinite;
      animation-name: change;
      animation-duration: 10s;
      animation-iteration-count: infinite;

      span {
        padding:20px;
      }

      @include forTablet {
        padding-left: 80px;
        font-size: 1.5rem;
      }
      @include forPc {
        font-size: 2.2rem;
      }
      &__item {
        line-height: 40px;
        margin: 0;
      }
    }
  }
}

@-webkit-keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@-webkit-keyframes change {
  0%,
  12.66%,
  100% {
    transform: translate3d(0, 0, 0);
  }
  16.66%,
  29.32% {
    transform: translate3d(0, -25%, 0);
  }
  33.32%,
  45.98% {
    transform: translate3d(0, -50%, 0);
  }
  49.98%,
  62.64% {
    transform: translate3d(0, -75%, 0);
  }
  66.64%,
  79.3% {
    transform: translate3d(0, -50%, 0);
  }
  83.3%,
  95.96% {
    transform: translate3d(0, -25%, 0);
  }
}

